export default {
	props: {
		// 加载中
		requesting: {
			type: Boolean,
			default: false
		},
		// 加载完毕
		end: {
			type: Boolean,
			default: false
		},
		// 控制空状态的显示
		emptyShow: {
			type: Boolean,
			default: false
		},
		// 当前列表长度
		listCount: {
			type: Number,
			default: 0
		},
		// 空状态的图片
		emptyUrl: {
			type: String,
			default: 'http://cdn.haofang.net/static/xfldDeveloper/msg/no_msg.png'
		},
		// 空状态的文字提示
		emptyText: {
			type: String,
			default: '暂时没有新数据'
		},
		// 是否有header
		hasTop: {
			type: Boolean,
			default: false
		},
		// 下拉刷新的高度
		refreshSize: {
			type: Number,
			default: 90
		},
		// 底部高度
		bottomSize: {
			type: Number,
			default: 0
		},
		// 颜色
		color: {
			type: String,
			default: '' //#3F82FD
		}
	},
	data() {
		return {
			mode: 'refresh', // refresh 和 more 两种模式
			successShow: false, // 显示success
			successTran: false, // 过渡success
			refreshStatus: 0, // 1: 下拉刷新, 2: 松开刷新, 3: 加载中, 4: 加载完成
			move: -45, // movable-view 偏移量
			scrollHeight1: 0, // refresh view 高度负值
			scrollHeight2: 0, // refresh view - success view 高度负值
			scrollTop: 0,
			overOnePage: false
		}
	},
	computed: {},
	watch: {
		requesting(newVal, oldVal) {
			this.requestingEnd(newVal, oldVal)
		},
		refreshSize(newVal, oldVal) {
			this.refreshSizeChange(newVal, oldVal)
		}
	},
	mounted() {
		this.init()
	},
	methods: {
		/**
		 * 处理 bindscrolltolower 失效情况
		 */
		scroll(e) {
			this.overOnePage = true;

			clearTimeout(this.timer);
			this.timer = setTimeout(() => {
				this.scrollTop = e.detail.scrollTop
			}, 100)
		},
		/**
		 * movable-view 滚动监听
		 */
		change(e) {
			const { refreshStatus } = this;

			// 判断如果状态大于3则返回
			if (refreshStatus >= 3) {
				return
			}

			let diff = e.detail.y;

			console.log('movable-view 滚动监听: ', diff);

			if (diff > 1) {
				this.refreshStatus = 2
			} else {
				this.oldY = diff
				this.refreshStatus = 1
			}
		},
		/**
		 * movable-view 触摸结束事件
		 */
		touchend() {
			const { refreshStatus } = this

			if (refreshStatus >= 3) {
				return
			}

			if (refreshStatus == 2) {
				// uni.vibrateShort()
				this.refreshStatus = 3
				this.move = 0
				this.mode = 'refresh'
				this.$emit('refresh')
			} else if (refreshStatus == 1) {
				this.move = this.oldY

				this.$nextTick(() => {
					this.move = this.scrollHeight1
				})
			}
		},
		/**
		 * 加载更多
		 */
		more() {
			if (!this.end) {
				this.mode = 'more'
				this.$emit('more')
			}
		},
		/**
		 * 监听 requesting 字段变化, 来处理下拉刷新对应的状态变化
		 */
		requestingEnd(newVal, oldVal) {
			if (this.mode == 'more') {
				return
			}

			if (oldVal === true && newVal === false) {
				this.successShow = true
				this.refreshStatus = 4
				this.move = this.scrollHeight2

				setTimeout(() => {
					setTimeout(() => {
						this.successTran = true
						this.move = this.scrollHeight1

						setTimeout(() => {
							this.refreshStatus = 1
							this.successShow = false
							this.successTran = false
							// this.move = this.scrollHeight1
						}, 350)
					}, 1500)
				}, 650)
			} else {
				if (this.refreshStatus != 3) {
					this.refreshStatus = 3
					this.move = 0
				}
			}
		},
		/**
		 * 监听下拉刷新高度变化, 如果改变重新初始化参数, 最小高度80rpx
		 */
		refreshSizeChange(newVal, oldVal) {
			if (newVal <= 80) {
				this.refreshSize = 80
			}
			setTimeout(() => {
				this.init()
			}, 0)
		},
		/**
		 * 初始化scroll组件参数, 动态获取 下拉刷新区域 和 success 的高度
		 */
		init() {
			let query = uni.createSelectorQuery().in(this)

			query.select('#refresh').boundingClientRect()
			query.select('#success').boundingClientRect()

			query.exec(res => {
				console.log('init: ', res, res[0].height, res[1].height, res[1].height - res[0].height)

				this.scrollHeight1 = -res[0].height
				this.scrollHeight2 = res[1].height - res[0].height
			})
		}
	}
}
